import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppComponent } from './app.component';
import { ChartScreenComponent } from './chart-screen/chart-screen.component';
import { PollScreenComponent } from './poll-screen/poll-screen.component';
import { RouterModule, Routes} from '@angular/router';
import { HighchartsChartModule } from 'highcharts-angular';
import { TimerComponent } from './timer/timer.component';

const routes: Routes = [
  {
    path: 'poll/:session',
    component: PollScreenComponent
  },
  {
    path: 'chart/:session',
    component: ChartScreenComponent
  },
  {
    path: ':session',
    redirectTo: '/chart/:session',
    pathMatch: 'full'
  }
];

@NgModule({
  declarations: [
    AppComponent,
    ChartScreenComponent,
    PollScreenComponent,
    TimerComponent
  ],
  imports: [
    HighchartsChartModule,
    BrowserModule,
    RouterModule.forRoot(routes)
   
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
