import { Component, OnInit } from '@angular/core';
import { VotingService } from '../../shared/voting.service';
import { ActivatedRoute } from '@angular/router';

declare let $: any;
@Component({
  selector: 'app-poll-screen',
  templateUrl: './poll-screen.component.html',
  styleUrls: ['./poll-screen.component.css']
})

export class PollScreenComponent implements OnInit {
  session: string
  canVote: boolean
  canVoteSession: boolean
    rangenumber: number = 0;

  constructor(private votingService: VotingService, private route: ActivatedRoute) { 
    this.route.params.subscribe( params => {
      this.session = params.session
    });
  }

  vote(score){
    if(this.canVote && this.canVoteSession){
      this.votingService.castVote(score, this.session);
    }
  }
  ngOnInit() {
    this.votingService.voteFlagRefresh(this.session).subscribe((canVoteSession:boolean) => {
      this.canVoteSession = canVoteSession
      console.log("data from service on pollscreen" + canVoteSession)
    });
    
    this.votingService.isEnabled$.subscribe((canVote:boolean) => {
      this.canVote = canVote
    });

    $(document).ready(function(){
      

      $(".slider").each(function() {
          let self = $(this);
          let slider = self.slider({
              create: function() {
                  self.find('.text strong').text(self.slider('value'));
                  setPathData(self.find('.smiley').find('svg path'), self.slider('value'));
              },
              slide: function(event, ui) {
                  self.find('.text strong').text(ui.value);
                //   $(".btn").value(ui.value);
                  setPathData(self.find('.smiley').find('svg path'), ui.value);

              },
              range: 'min',
              min: 1,
              max: 10,
              value: 1,
              step: 1
          });
      });
      function setPathData(path, value) {
        let firstStep = 6 / 10 * value;
        let secondStep = 2 / 10 * value;
        path.attr('d', 'M1,' + (7 - firstStep) + ' C6.33333333,' + (2 + secondStep) + ' 11.6666667,' + (1 + firstStep) + ' 17,' + (1 + firstStep) + ' C22.3333333,' + (1 + firstStep) + ' 27.6666667,' + (2 + secondStep) + ' 33,' + (7 - firstStep));

      }  
      });
   
}


}
