import {Component, OnInit, Input } from '@angular/core';

declare let TweenMax: any;
declare let TweenLite: any;
declare let Expo: any;

@Component({
	selector: 'app-timer',
	templateUrl: './timer.component.html',
	styleUrls: ['./timer.component.css']
})


export class TimerComponent implements OnInit {

	@Input('time') time: string;
	@Input('label') label: string;
	constructor() {}



	initTimer(t) {
		TweenLite.defaultEase = Expo.easeOut;

		initTimer(t)
		var reloadBtn = document.querySelector('.reload');
		var timerEl = document.querySelector('.timer');

		function initTimer(t) {

			var self = this,
				timerEl = document.querySelector('.timer'),
				hoursGroupEl = timerEl.querySelector('.hours-group'),
				minutesGroupEl = timerEl.querySelector('.minutes-group'),
				secondsGroupEl = timerEl.querySelector('.seconds-group'),

				hoursGroup = {
					firstNum: hoursGroupEl.querySelector('.first'),
					secondNum: hoursGroupEl.querySelector('.second')
				},

				minutesGroup = {
					firstNum: minutesGroupEl.querySelector('.first'),
					secondNum: minutesGroupEl.querySelector('.second')
				},

				secondsGroup = {
					firstNum: secondsGroupEl.querySelector('.first'),
					secondNum: secondsGroupEl.querySelector('.second')
				};

			var time = {
				hrs: t.split(':')[0],
				min: t.split(':')[1],
				sec: t.split(':')[2]
			};
			console.log(t)
			var timeNumbers;

			function updateTimer() {

				var timestr;
				var date = new Date();

				date.setHours(time.hrs);
				date.setMinutes(time.min);
				date.setSeconds(time.sec);

				var newDate = new Date(date.valueOf() + 1000);
				var temp = newDate.toTimeString().split(" ");
				var tempsplit = temp[0].split(':');

				time.hrs = tempsplit[0];
				time.min = tempsplit[1];
				time.sec = tempsplit[2];

				timestr = time.hrs + time.min + time.sec;
				timeNumbers = timestr.split('');
				updateTimerDisplay(timeNumbers);


				if (timestr != '0000')
					setTimeout(updateTimer, 1000);

			}

			function updateTimerDisplay(arr) {

				animateNum(hoursGroup.firstNum, arr[0]);
				animateNum(hoursGroup.secondNum, arr[1]);
				animateNum(minutesGroup.firstNum, arr[2]);
				animateNum(minutesGroup.secondNum, arr[3]);
				animateNum(secondsGroup.firstNum, arr[4]);
				animateNum(secondsGroup.secondNum, arr[5]);

			}

			function animateNum(group, arrayValue) {

				TweenMax.killTweensOf(group.querySelector('.number-grp-wrp'));
				TweenMax.to(group.querySelector('.number-grp-wrp'), 1, {
					y: -group.querySelector('.num-' + arrayValue).offsetTop
				});

			}

			setTimeout(updateTimer, 1000);

		}
  }
  


	ngOnInit() {
		this.initTimer(this.time); // other ways --> "0:15" "03:5" "5:2"
		console.log(this.time)
	}

  
}