import { Component, OnInit, OnDestroy } from '@angular/core';
import * as Highcharts from "highcharts/highstock";
import { VotingService } from '../../shared/voting.service';
import { Subscription } from 'rxjs';
import { ActivatedRoute } from '@angular/router';
import { SessionService } from '../../shared/session.service';

declare let require: any
const IndicatorsCore = require("highcharts/indicators/indicators");
IndicatorsCore(Highcharts);
const IndicatorZigZag = require("highcharts/indicators/zigzag");
IndicatorZigZag(Highcharts);
@Component({
  selector: 'app-chart-screen',
  templateUrl: './chart-screen.component.html',
  styleUrls: ['./chart-screen.component.css']
})
export class ChartScreenComponent implements OnInit, OnDestroy{
  private chartData;
  private subscription : Subscription;
  private updateFlag:boolean;
  Highcharts = Highcharts;
  chartOptions = {
    global : {
      useUTC : false
    },
    colors: ['#90ee7e', '#f45b5b', '#7798BF', '#aaeeee', '#ff0066',
    '#eeaaee', '#55BF3B', '#DF5353', '#7798BF', '#aaeeee'],
    chart: {
      type: 'spline',
      scrollablePlotArea: {
        minWidth: 600,
        scrollPositionX: 1
      },
      backgroundColor: {
        linearGradient: { x1: 0, y1: 0, x2: 1, y2: 1 },
        stops: [
            [0, '#2a2a2b'],
            [1, '#3e3e40']
        ]
    },
    style: {
        fontFamily: '\'Unica One\', sans-serif'
    },
    plotBorderColor: '#606063'
    },
    title: {
      style: {
        color: '#E0E0E3',
        textTransform: 'uppercase',
        fontSize: '20px'
    },
      text: ''
    },
    subtitle: {
      style: {
        color: '#E0E0E3',
        textTransform: 'uppercase'
    },
      text: ''
    },
    xAxis: {
      gridLineColor: '#707073',
        labels: {
            style: {
                color: '#E0E0E3'
            }
        },
        lineColor: '#707073',
        minorGridLineColor: '#505053',
        tickColor: '#707073',
        title: {
            style: {
                color: '#A0A0A3'

            }
        },
      offset:-125,
      plotBands: [{ // visualize the weekend
        from: Date.UTC(2018, 11, 7, 0, 0, 0),
        to: Date.UTC(2018, 11, 7, 0, 15, 0),
        color: 'rgba(68, 170, 29, .2)'
      },
        { // visualize the weekend
        from: Date.UTC(2018, 11, 7, 0, 15, 0),
        to: Date.UTC(2018, 11, 7, 0, 30, 0),
        color: 'rgba(50, 34, 150, .2)'
      },
       { // visualize the weekend
         from: Date.UTC(2018, 11, 7, 0, 30, 0),
        to: Date.UTC(2018, 11, 7, 0, 45, 0),
        color: 'rgba(90, 70, 189, .2)'
      },
      { // visualize the weekend
         from: Date.UTC(2018, 11, 7, 0, 45, 0),
        to: Date.UTC(2018, 11, 7, 0, 60, 0),
        color: 'rgba(50, 70, 140, .2)'
      }],
      type: 'datetime',
      dateTimeLabelFormats:{minute: '%H:%M',}
    },
    yAxis: {
      max: 10,
      min:0,
      gridLineColor: '#707073',
        labels: {
            style: {
                color: '#E0E0E3'
            }
        },
        lineColor: '#707073',
        minorGridLineColor: '#505053',
        tickColor: '#707073',
        tickWidth: 1,
        title: {
            style: {
                color: '#A0A0A3'
            },
            text: 'Current Interest Level'
        },
      minorGridLineWidth: 0,
      gridLineWidth: 0,
      alternateGridColor: null,
    },
    tooltip: {
      backgroundColor: 'rgba(0, 0, 0, 0.85)',
      style: {
          color: '#F0F0F0'
      }
    },
    plotOptions: {
      spline: {
        lineWidth: 4,
        states: {
          hover: {
            lineWidth: 5
          }
        },
        marker: {
          enabled: false
        },
        //pointInterval: 5000, // one hour
        //pointStart: 0
      }
    },
    legend: {
        itemStyle: {
            color: '#E0E0E3'
        },
        itemHoverStyle: {
            color: '#FFF'
        },
        itemHiddenStyle: {
            color: '#606063'
        }
    },
    labels: {
      style: {
          color: '#707073'
      }
  },
    series: [{
      showInLegend: false,
      name: 'Rating',
      // data: [
      //   3.7, 3.3, 3.9, 5.1, 3.5, 3.8, 4.0, 5.0, 6.1, 3.7, 3.3, 6.4
      // ]
      data: []
    }],
    navigation: {
      menuItemStyle: {
        fontSize: '10px'
      }
    },
    legendBackgroundColor: 'rgba(0, 0, 0, 0.5)',
    background2: '#505053',
    dataLabelsColor: '#B0B0B3',
    textColor: '#C0C0C0',
    contrastTextColor: '#F0F0F3',
    maskColor: 'rgba(255,255,255,0.3)'
  }
  session: string
  constructor(private resultService:VotingService, private route: ActivatedRoute, private sessionService:SessionService) {
    
    this.route.params.subscribe( params => {
      this.sessionService.setSession(params.session)
      this.session = params.session
    });
   }

  ngOnInit() {  
    console.log("inside init")
    this.subscription=this.resultService.onNewVote(this.session).subscribe(val=> {
      console.log(val)
      this.chartOptions.series[0].data=val.data;
      this.chartOptions.title.text=val.meetingTitle;
      //this.chartOptions.plotOptions.spline.pointStart=val[0][0];
      this.updateFlag=true
    });
    
  }
  ngOnDestroy(){
    console.log('Component destroyed');
    this.subscription.unsubscribe();

  }

}
