import { Injectable } from '@angular/core';
import * as io from 'socket.io-client';
import { Observer, Observable, BehaviorSubject } from 'rxjs';
@Injectable({
  providedIn: 'root'
})
export class VotingService {
  private isEnabled:BehaviorSubject<boolean>
  public isEnabled$:Observable<boolean>
  constructor() {
    this.isEnabled = new BehaviorSubject<boolean>(true);
    this.isEnabled$ = this.isEnabled.asObservable();
 
   }
  private socket=io('/');
  castVote(score: string, session:string) {
    console.log("vote On" + session)
    this.socket.emit('vote/',{'sessionId' : session,'score': score});
    this.isEnabled.next(false)
    setTimeout(() => {
      this.isEnabled.next(true)
    }, 300000);
  }  

  onNewVote(session:string):Observable<any> {
    console.log(session)
      this.socket.emit('requestResults/',{'sessionId' : session});
      let observable = new Observable(observer => {
        this.socket.on('showResults/'+ session, (data) => {
          observer.next(data);    
          console.log('Data on toic' +data)
        });
       }) ;   
      return observable;
    }  
    
    voteFlagRefresh(session: string):Observable<any> {
                this.socket.emit('canIVote/',{'sessionId' : session});
                let observable = new Observable(observer => {
                  this.socket.on('canVote/'+ session, (data) => {
                    observer.next(data);    
                    console.log('Data on flag refresh ' +data)
                  });
                 }) ;   
                 return observable;
      }  
  }



